@charset "UTF-8";
.single .main-content .content,
.page .main-content .content {
  margin: 60px 0;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  gap: 60px;
}
@media (min-width: 768px) {
  .single .main-content .content,
  .page .main-content .content {
    flex-direction: row-reverse;
  }
}
.single .main-content .content .formatted-text,
.page .main-content .content .formatted-text {
  min-width: 350px;
  flex: 1 0 calc(100% - 370px);
}
.single .main-content .content .gallery-slider,
.page .main-content .content .gallery-slider {
  margin-top: 35px;
  position: relative;
}
.single .main-content .content .gallery-slider .photos,
.page .main-content .content .gallery-slider .photos {
  z-index: 0;
}
.single .main-content .content .gallery-slider .photo,
.page .main-content .content .gallery-slider .photo {
  height: 314px;
  position: relative;
  display: flex;
}
.single .main-content .content .gallery-slider .photo::after,
.page .main-content .content .gallery-slider .photo::after {
  content: "";
  width: calc(100% - 50px);
  height: calc(100% - 20px);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  background-color: rgba(var(--primary-color), 0.6);
  transition: 0.3s linear;
}
.single .main-content .content .gallery-slider .photo:hover::after,
.page .main-content .content .gallery-slider .photo:hover::after {
  opacity: 1;
}
.single .main-content .content .gallery-slider .photo img,
.page .main-content .content .gallery-slider .photo img {
  width: 100%;
  height: 100%;
  margin: 0 15px;
  display: block;
  object-fit: cover;
  object-position: center center;
}
.single .main-content .content .gallery-slider .swiper-navigation .arrow,
.page .main-content .content .gallery-slider .swiper-navigation .arrow {
  all: unset;
  width: 50px;
  height: 60px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(var(--primary-color));
  cursor: pointer;
  transition: 0.3s linear;
}
.single .main-content .content .gallery-slider .swiper-navigation .arrow:hover,
.page .main-content .content .gallery-slider .swiper-navigation .arrow:hover {
  background-color: rgb(var(--secondary-color));
}
.single .main-content .content .gallery-slider .swiper-navigation .arrow::after,
.page .main-content .content .gallery-slider .swiper-navigation .arrow::after {
  font-size: 14px;
  font-weight: 600;
  color: #fff;
}
.single .main-content .content .gallery-slider .swiper-navigation .arrow.swiper-button-next,
.page .main-content .content .gallery-slider .swiper-navigation .arrow.swiper-button-next {
  right: 0;
}
.single .main-content .content .gallery-slider .swiper-navigation .arrow.swiper-button-prev,
.page .main-content .content .gallery-slider .swiper-navigation .arrow.swiper-button-prev {
  left: 0;
}
.single .main-content .content .sidebar,
.page .main-content .content .sidebar {
  max-width: 100%;
  flex: 1 0 310px;
  display: flex;
  flex-direction: column;
  row-gap: 60px;
}
.single .main-content .content .sidebar .widget,
.page .main-content .content .sidebar .widget {
  max-width: 100%;
  width: 100%;
  padding: 40px;
  background-color: rgba(var(--secondary-color));
  display: flex;
  flex-direction: column;
}
.single .main-content .content .sidebar .widget p,
.single .main-content .content .sidebar .widget span,
.single .main-content .content .sidebar .widget a,
.page .main-content .content .sidebar .widget p,
.page .main-content .content .sidebar .widget span,
.page .main-content .content .sidebar .widget a {
  color: #fff;
}
.single .main-content .content .sidebar .widget ul,
.page .main-content .content .sidebar .widget ul {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  list-style: disc;
  list-style-position: inside;
}
.single .main-content .content .sidebar .widget ul li,
.page .main-content .content .sidebar .widget ul li {
  width: 100%;
  position: relative;
  padding-left: 2.5rem;
  display: block;
  text-align: left;
  font-family: var(--poppins);
  font-size: 1.6rem;
  color: #fff;
}
.single .main-content .content .sidebar .widget ul li::before,
.page .main-content .content .sidebar .widget ul li::before {
  content: "‚óè";
  width: 2rem;
  height: 2rem;
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  font-size: 2rem;
  color: #fff;
}
.single .main-content .content .sidebar .widget ul li:hover a,
.page .main-content .content .sidebar .widget ul li:hover a {
  color: rgba(var(--tertiary-color));
}
.single .main-content .content .sidebar .widget ul li a,
.page .main-content .content .sidebar .widget ul li a {
  display: inline;
  font-family: var(--poppins);
  color: #fff;
  transition: 0.2s ease-in-out;
}
.single .main-content .content .sidebar .widget__title,
.page .main-content .content .sidebar .widget__title {
  margin-bottom: 20px;
  font-weight: 700;
  color: #fff;
}
.single .main-content .content .sidebar .widget.sidebar-menu,
.page .main-content .content .sidebar .widget.sidebar-menu {
  padding: 0;
  background-color: transparent;
}
.single .main-content .content .sidebar .widget.sidebar-menu .widget__title,
.page .main-content .content .sidebar .widget.sidebar-menu .widget__title {
  color: rgba(var(--primary-color));
}
.single .main-content .content .sidebar .widget.sidebar-menu ul.menu,
.page .main-content .content .sidebar .widget.sidebar-menu ul.menu {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.single .main-content .content .sidebar .widget.sidebar-menu ul.menu li.menu-item,
.page .main-content .content .sidebar .widget.sidebar-menu ul.menu li.menu-item {
  padding: 0;
  display: flex;
}
.single .main-content .content .sidebar .widget.sidebar-menu ul.menu li.menu-item::before,
.page .main-content .content .sidebar .widget.sidebar-menu ul.menu li.menu-item::before {
  content: none;
}
.single .main-content .content .sidebar .widget.sidebar-menu ul.menu li.menu-item a,
.page .main-content .content .sidebar .widget.sidebar-menu ul.menu li.menu-item a {
  padding: 18px 0;
  border-bottom: 1px solid #eeeeee;
  display: block;
  font-size: 16px;
  font-weight: 500;
  line-height: normal;
  position: relative;
  text-align: left;
  transition: 0.3s linear;
  color: #616263;
}
.single .main-content .content .sidebar .widget.sidebar-menu ul.menu li.menu-item a:hover,
.page .main-content .content .sidebar .widget.sidebar-menu ul.menu li.menu-item a:hover {
  color: rgba(var(--secondary-color));
}
.single .main-content .content .sidebar .widget.sidebar-menu ul.menu li.menu-item a:first-child,
.page .main-content .content .sidebar .widget.sidebar-menu ul.menu li.menu-item a:first-child {
  padding-top: 0;
}
.single .main-content .content .sidebar .widget.sidebox,
.page .main-content .content .sidebar .widget.sidebox {
  padding: 0;
  position: relative;
}
.single .main-content .content .sidebar .widget.sidebox .bg-img,
.page .main-content .content .sidebar .widget.sidebox .bg-img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  object-fit: cover;
  object-position: center center;
}
.single .main-content .content .sidebar .widget.sidebox .sidebox__inner,
.page .main-content .content .sidebar .widget.sidebox .sidebox__inner {
  width: 100%;
  height: 100%;
  padding: 40px;
  position: relative;
  z-index: 1;
  background-color: rgba(var(--tertiary-color), 0.6);
}
.single .main-content .content .sidebar .widget.sidebox .sidebox__inner ul li::before,
.page .main-content .content .sidebar .widget.sidebox .sidebox__inner ul li::before {
  color: rgba(var(--secondary-color));
}
.single .main-content .content .sidebar .widget.sidebox .sidebox__inner .btn.primary:hover,
.page .main-content .content .sidebar .widget.sidebox .sidebox__inner .btn.primary:hover {
  background-color: rgba(var(--secondary-color));
}
.single .main-content .content .sidebar .widget.sidebox .btn,
.page .main-content .content .sidebar .widget.sidebox .btn {
  width: 100%;
  margin-top: 15px;
}
.single .main-content .content .sidebar .widget .wpforms-container .wpforms-field-row,
.page .main-content .content .sidebar .widget .wpforms-container .wpforms-field-row {
  max-width: 100%;
}